<template>
  <span></span>
</template>

<script>
  import Typed from 'typed.js';

  export default {
    name: 'typed',

    data() { return { typed: null } },

    props: {
      strings: {
        required: true,
        type: Array
      },
      typeSpeed: {
        required: false,
        default: 30,
        type: Number
      }
    },

    mounted() {
      this.typed = new Typed(this.$el, {
        strings: this.strings,
        typeSpeed: this.typeSpeed,
        backSpeed: 30,
        loop: true,
        loopCount: Infinity,
        backDelay: 500,
        startDelay: 1000
      });
    },

    beforeDestroy() {
      this.typed.destroy();
    }
  }
</script>